.wallet {
  background-color: #00000077;
  position: absolute;
  display: none;
}

/* wrappers */

* {
  background-size: contain;
  background-repeat: no-repeat;
  box-sizing: border-box;
  font-size: 20px;
}

.container {
  background-repeat: repeat;
  background-color: #412a85;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  overflow: auto;
  color: #000;
  font-family: 'Montserrat', sans-serif;
}

.wrap {
  max-width: 1024px;
  margin: 0 auto;
}

.content {
  background-color: #fff;
  margin: 20px;
  display: flex;
  flex-direction: column;
}

/* sections */

.hero {
  display: flex;
  color: #fff;
  flex-direction: row;
  padding: 20px;
  padding-top: 83px;
  /* padding-bottom: 50px; */
  /* min-height: 70vh; */
}
.cta {
  width: 50%;
}
.cta .hl {
  font-size: 40px;
  font-weight: 700;
  text-shadow: 0 0 2px black;
}
.cta .cp {
  text-shadow: 0 0 2px black;
}
.cp {
  margin-top: 10px;
}
.mi {
  display: block;
  width: 50%;
  padding: 184px 0 0 50px;
}
.mi img {
  width: 100%;
  padding: 0;
  margin: 0;
}

.mint {
  background: #00000080;
  margin: 10px 0px;
  border-radius: 4px;
  position: relative;
  padding: 20px;
}
.mint p {
  padding-left: 0;
}

.price {
  padding: 10px 20px;
  font-weight: 700;
  position: absolute;
  right: 0;
  bottom: 27px;
  display: flex;
  align-items: flex-end;
}
.mintbtn {
  background-color: #fff !important;
  color: #000 !important;
  font-weight: 700 !important;
}

.nftinfo {
  text-shadow: 0 0 2px black;
  margin-bottom: 20px;
  text-align: center;
}

.about{
  display: flex;
  justify-content: center;
  text-align: center;
  /* padding: 20px; */
}
.aboutp {
  max-width: 80%;
  display: inline-block;
  text-align: left;
}

.details{
  display: flex;
  justify-content: center;
  text-align: center;
  /* padding: 20px; */
  background-color: #ffe145
}
.hl {
  font-size: 40px;
  font-weight: 700;
  padding-top: 20px;
}

.items {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 20px;
  background-color: #ffe145
}
.item {
  text-align: center;
  padding: 20px;
  flex: 1;
}

.item .blk {
  width: 80px;
  height: 80px;
  min-width: 80px;
  /* background-color: #000; */
  display: inline-block;
  margin-bottom: 20px;
  flex: 0;
}
.icon {
  width: 100%;
}

.item .ctx {
  flex: 1;
}

.footer {
  text-align: center;
  height: 130px;
  color: #fff;
  padding: 20px;
}

/* elems */

strong { 
  display: block;
}

p {
  color: #333;
}

.spacer {
  padding: 20px 0;
  width: 100%;
}
.somelinks {
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.somelinks a div {
  border-radius: 100%;
  width: 60px;
  height: 60px;
  margin: 10px;
  background-size: cover;
  background-position: center center;
}
.details .somelinks a div {
  width: 90px;
  height: 90px;
  margin: 30px;
}

.blocklink {
  margin-top: 10px;
  display: block;
}

a:link, a:visited {
  color: #000;
  text-decoration: none;
  font-weight: 600;
}
a:hover {
  color: purple;
  text-decoration: underline;
  
}



.aboutmark {
  background-color: #ff3399;
  padding: 8px;
  color: #fff;
  text-align: left;
  display: inline;
  line-height: 45px;
}

@media only screen and (max-width: 600px) {
  .container {
    background-repeat: repeat;
  }
  .content {
    margin: 0;
    margin-top: 0;
    border-bottom: none;
  }
  .hero {
    flex-direction: column;
  }
  
  .cta {
    width: 100%;
  }
  .mi {
    display: block;
    width: 100vw;
    padding: 0;
    margin: 40px -20px 0 -20px;
  }

  section p {
    padding: 0px 20px;
  }
  .item p {
    padding: 0;
  }

  .items {
    flex-direction: column;
  }
  .item {
    text-align: left;
    display: flex;
    flex-direction: row;
  }
  .item .blk {
    margin-right: 10px;
    margin-bottom: 0px;
    min-width: 22px;
  }
  .footer {
    margin: 0 -20px;
    font-size: 12px;
  }
  .aboutp {
    max-width: 100%;
    text-align: left;
  }
}